html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ol,
ul {
  list-style: none;
}

a {
  text-decoration: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}

.clearfix:after {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

body {
  font-size: 14px;
  line-height: 1.5;
  font-family: "Segoe UI", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #333;
  background: #fff;
}

.wrapper {
  margin: 0 auto;
  max-width: 1200px;
}

.title {
  position: relative;
  z-index: 1;
  font-size: 40px;
  line-height: 1;
  color: #142c44;
  text-align: center;
}

.title:before {
  content: attr(data-text);
  position: absolute;  
  z-index: -1;
  margin:-40px 0 0 -80px;
  font-size: 80px;
  line-height: 1;
  color: #f0f4f8;
}

@media only screen and (max-width: 1400px) {
  .wrapper {
    width: 900px;
  }
}


.feature {
  position: relative;
  padding: 100px 0;
  height: 720px;
  background: #f8fcff;
}

.feature-list {
  margin-top: 80px;
}

.feature-list li {
  width: 33.33%;
  display: inline-block;
  will-change: transform;
}

.feature-list li .feature-box {
  position: relative;
  margin: auto;
  width: 194px;
  text-align: center;
}

.feature-box .feature-image {
  position: relative;
  z-index: 1;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin: 30px auto;
  background: #fff;
  -webkit-transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, -webkit-transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out;
  transition: box-shadow 0.3s ease-out, transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.feature-box:hover .feature-image {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
}

.feature-box .feature-image img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 44px;
  height: 44px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.feature-box p {
  font-size: 20px;
  line-height: 40px;
}

.feature-point-wrapper {
  position: absolute;
  width: 0;
  left: 50%;
  top: 0;
}

.feature-point-wrapper .point-0 {
  width: 4px;
  height: 4px;
}

.feature-point-wrapper .point-2,
.feature-point-wrapper .point-ring {
  width: 10px;
  height: 10px;
}

.feature-point-wrapper .point-ring {
  border-style: solid;
  border-width: 1px;
  background: transparent !important;
}

.feature-point-wrapper .point-1 {
  width: 6px;
  height: 6px;
}

.feature-point-wrapper .point-3 {
  width: 15px;
  height: 15px;
}

.feature-point-wrapper i {
  display: inline-block;
  border-radius: 100%;
  position: absolute;
  opacity: 0;
  -webkit-transform: translate(0, 30px);
          transform: translate(0, 30px);
}
.scene {
    padding: 150px 0;
    height: 505px;
}

.scene-list li {
    margin-top: 60px;
    display: inline-block;
    width: 33.33%;
}

.scene-list .scene-item {
    margin: 0 auto;
    padding-bottom: 25px;
    width: 340px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 5px 10px 20px #ddd;
    overflow: hidden;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.scene-list li .scene-item:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.scene-item img {
    width: 340px;
    height: 270px;
}

.scene-item h3 {
    margin-top: 10px;
    font-size: 22px;
    line-height: 40px;
    color: #142c44;
}

.scene-item p {
    font-size: 14px;
    line-height: 25px;
    color: #636a7f;
}

.effect {
    position: relative;
    height: 685px;
    padding-bottom: 150px;
}

.effect-list {
    margin: 0 -25px;
}

.effect-list li {
    display: inline-block;
    width: 50%;
}

.effect-item h3 {
    margin-top: 40px;
    font-size: 30px;
    line-height: 100px;
    text-align: center;
}

.effect-item .effect-img {
    margin: 0 50px;
    text-align: center;
    border-radius: 10px;
    box-shadow: 5px 10px 20px #ddd;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.effect-item:hover .effect-img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.effect-item .effect-img img {
    width: 500px;
}

@media only screen and (max-width: 1400px) {
    .scene-list .scene-item {
        width: 260px;
    }

    .scene-item img {
        width: 260px;
        height: 205px;
    }
    
    .effect-list {
        margin: 0;
    }

    .effect-item h3 {
        font-size: 22px;
    }

    .effect-item .effect-img img {
        width: 300px;
    }

}
